*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    height: 100vh;
    font-family: 'Poppins';
    color: white;
}

.webgl
{
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}
.container { 
    position: absolute;
    z-index: 1;
    width:100%;
    height: 100vh;  
    display: grid;
    place-content: center;

}

.container2 { 
    position: absolute;
    z-index: 1;
    width:100%;
    height: 100vh;  
    display: grid;
    place-content: center;
    background-color: black;
    opacity: 10%;
}

.content {
    display: flex;
    gap: 5em;
    width: 100%;
    padding-top: 3em;
    position: relative;
}

.content:before{

    content: '';
    position:absolute;
    top: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid white;
    transform: scaleX(1);
}

h1{

    font-size: 4rem;
    width:  50vw;
    line-height: 97%;
    text-align: right;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
h1, p {
    flex-basis: 0;
    flex-grow: 1;
    clip-path: polygon( 0 0,100% 0, 100% 0, 0 0 );
}

p {
    font-size:  1.3rem;
    width: 40vh;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

